$button-color: #282828;
$button-hover-color: #181818;
$body-color: #181818;
$background-color: #1d1d1d;
$font-color: #c2c2ac;
$button-border-color: #66666699;
$button-border-hover-color: #999999;
$link-color: rgb(199, 191, 118);
$default-font: "Oxygen", sans-serif;

p {
  font-family: $default-font;
  font-weight: 300;
  font-size: 2.5vh;
  margin-top: 0.5em;
  margin-bottom: 0;
  color: $font-color;
}

.button {
  font-family: $default-font;
  width: calc(3vh * 7);
  line-height: 200%;
  background-color: $button-color;
  color: $font-color;
  text-align: center;
  font-size: 2.5vh;
  position: relative;
  transition: width 0.2s, border 0.2s;
  min-height: 0;
  border: 1px solid $button-border-color;
  border-radius: 0.3em;
  z-index: 3;
  &.primary {
    background-color: $font-color;
    transition: background-color 0.2s, width 0.2s;
    color: $button-color;
  }
}

.button:hover {
  font-family: $default-font;
  width: calc(3vh * 5.5);
  transition: width 0.2s, border 0.2s;
  border: 1px solid $button-border-hover-color;
  &.primary {
    background-color: $link-color;
    transition: background-color 0.2s, width 0.2s;
    color: $button-color;
  }
}

a {
  color: $link-color;
  font-family: $default-font;
}

a.button-link {
  color: $font-color;
  text-decoration: none;
  position: absolute;
  min-width: 100%;
  min-height: 100%;
  right: 0;
  font-family: $default-font;
}

.title {
  font-size: 6vh;
  margin-top: 0;
  font-weight: 700;
  margin-bottom: 0;
  font-family: $default-font;
}

.subtitle {
  margin-top: 1.5em;
  margin-bottom: 0.5em;
  font-size: 3vh;
  font-weight: 500;
  font-family: $default-font;
}

.fa {
  padding: 0.2vw;
  font-size: 2vw !important;
  width: 2vw;
  height: 2vw;
  border-radius: 20%;
  text-align: center;
  text-decoration: none;
  position: relative;
  background: #000;
  color: #d6d6c3;
}

.page-grid-cell-fill-container {
  display: table;
  min-height: 100%;
}

.page-grid-cell-fill-main {
  display: table-row;
  height: 0px;
}
.page-grid-cell-fill-rest {
  display: table-row;
  position: relative;
  min-height: 100%;
}
.bottom-element {
  margin: 0;
  position: absolute;
  bottom: 0%;
  left: 50%;
  -ms-transform: translate(-50%, 0%);
  transform: translate(-50%, 0%);
}
.page-grid-cell-screens {
  margin: auto;
  display: flex;
  align-items: center;
  align-self: stretch;
  justify-content: center;
  gap: 2%;
  margin: auto;
}
.text-input {
  background-color: $button-color;
  box-sizing: border-box;
  border: 1px solid $button-border-color;
  padding: 0.2em;
  font-size: 2.5vh;
  color: $font-color;
  font-family: $default-font;
  transition: border 0.2s;
  margin: 0.2em;
}

.text-input:hover {
  border: 1px solid $button-border-hover-color;
}

.video-showcase {
  width: 100vw;
  height: 100vh;
  background-color: #00000088;
  z-index: 500;
  position: fixed;
  left: 0;
  justify-content: center;
  align-items: center;
  transform: translateY(-10%);
  transition: opacity 0.5s;
  opacity: 0%;
  &.show {
    opacity: 100%;
    transition: opacity 0.5s;
  }
}

.video-box {
  transform: scale(70%, 70%);
  display: grid;
}
